import './style.css';
import {$,jQuery} from 'jquery';
import {Map, View} from 'ol';
import {Tile as TileLayer, Vector as VectorLayer} from 'ol/layer';
import VectorSource from 'ol/source/Vector';
import TileWMS from 'ol/source/TileWMS';
import GeoJSON from 'ol/format/GeoJSON';
import OSM from 'ol/source/OSM';
import {bbox as bboxStrategy} from 'ol/loadingstrategy';
import {Stroke, Style, Fill, Circle} from 'ol/style';

const domSource = new VectorSource({
  format: new GeoJSON(),
  url: function (extent) {
    return (
      'https://wms.qgiscloud.com/peterbetlem/rnd?service=WFS&request=GetFeature' +
      '&typename=doms&' +
      'outputformat=geojson'
    );
  },
  attributions: '© <a href="https://peterbetlem.github.io"' +
  'target="_blank">BetlemTech / geomonkey AS</a>'
});

const photoSource = new VectorSource({
  format: new GeoJSON(),
  url: function (extent) {
    return (
      'https://wms.qgiscloud.com/peterbetlem/rnd?service=WFS&request=GetFeature' +
      '&typename=photo_sphere&' +
      'outputformat=geojson'
    );
  },
  attributions: '© <a href="https://peterbetlem.github.io"' +
  'target="_blank">BetlemTech / geomonkey AS</a>'
});

const doms = new VectorLayer({
  source: domSource,
  style: new Style({
    stroke: new Stroke({
      color: 'rgba(0, 0, 255, 1.0)',
      width: 2,
    }),
    fill: new Fill({
      color: 'rgba(0, 0, 255, 0.3)',
      width: 2,
    }),
  }),
  title: "test",
});

const photospheres = new VectorLayer({
  source: photoSource,
});

const swissWMS = new TileLayer({
  source: new TileWMS({
    url: 'https://wms.geo.admin.ch/',
    params: {
    //  'LAYERS': 'ch.swisstopo.landeskarte-farbe-10',
      'LAYERS': 'ch.swisstopo.pixelkarte-farbe',
      'FORMAT': 'image/jpeg',
    },
    attributions:
    '© <a href="https://wms.geo.admin.ch/?REQUEST=GetCapabilities&SERVICE=WMS&VERSION=1.0.0"' +
    'target="_blank">swisstopo / geo.admin.ch</a>',
    serverType: 'mapserver',
    crossOrigin: 'anonymous',
    // Countries have transparency, so do not fade tiles:
    transition: 0,
  }),
});

const map = new Map({
  target: 'map',
  layers: [
    //new TileLayer({
    //  source: new OSM()
    //}),
    swissWMS,
    doms,
    photospheres
  ],
  view: new View({
    center: [1120885.7548, 5889644.3389],
    zoom: 9
  })
});

const selectionStyleMP = new Style({
  fill: new Fill({
    color: 'rgba(0, 0, 255, 0.7)',
  }),
  stroke: new Stroke({
    color: [0, 153, 255, 1],
    width: 3,
  }),
});
const selectionStylePoint = new Style({
  image: new Circle({
    radius: 7,
    fill: new Fill({color: 'black'}),
    stroke: new Stroke({
      color: [255,0,0], width: 2
    }),
  })
});

let selectedFeature;
const displayFeatureInfo = function (pixel) {
  if (selectedFeature) {
    selectedFeature.setStyle(null);
  }
  selectedFeature = map.forEachFeatureAtPixel(
    pixel,
    (feature, layer) => feature,
  );

  const info = document.getElementById('info');
  if (selectedFeature) {
    console.log(info.innerHTML)
    switch (selectedFeature.getGeometry().getType()) {
      case "MultiPolygon":
        console.log("MP")
        selectedFeature.setStyle(selectionStyleMP);
        if (selectedFeature.values_.doi) {
          info.innerHTML = '<center>Copyright and use policy:<br><a href="https://doi.org/' + selectedFeature.values_.doi+'" target=blank>' +
          'https://doi.org/' + selectedFeature.values_.doi+'</a></center>'
        } else {
          info.innerHTML = ''
        }
        info.innerHTML = info.innerHTML + '<br><div class="sketchfab-embed-wrapper"> '+
        '<iframe style="width:100%;height:400px;" frameborder="0" allowfullscreen mozallowfullscreen="true" '+
        'webkitallowfullscreen="true" allow="autoplay; fullscreen; xr-spatial-tracking" xr-spatial-tracking execution-while-out-of-viewport '+
        'execution-while-not-rendered web-share src="https://sketchfab.com/models/' + selectedFeature.values_.publ_sketchfab_id + '/embed"></iframe></div>'
        break
      case "Point":
        console.log("Point")
        selectedFeature.setStyle(selectionStylePoint);
        info.innerHTML = '<center>Copyright and use policy:<br><a href="https://doi.org/10.5281/zenodo.' + selectedFeature.values_.doi+'" target=blank>' +
        '<img src="https://zenodo.org/badge/DOI/10.5281/zenodo.'+selectedFeature.values_.doi+'.svg" alt="DOI"></a></center>' +
        '<center>' +
        '<a href="https://zenodo.org/record/' + selectedFeature.values_.doi+'/files/'+ selectedFeature.values_.uid + '.jpg?download=1' + '" target=_blank>Download Full Resolution </a>' +
        '<br>' +
        '<br><div class="sketchfab-embed-wrapper"> <iframe style="width:100%;height:100%;" allowfullscreen style="border-style:none;"'+
        ' src="https://cdn.pannellum.org/2.5/pannellum.htm#panorama='+selectedFeature.values_.api_link+'&autoLoad=false"></iframe></div>'
        break
        
    }

  }
};

map.on('click', (evt) => {
  displayFeatureInfo(evt.pixel)
});